.color-nav {
	background: rgba(7, 7, 6, 0.931);
	box-shadow: 1px -.5px 3px .5px rgb(122, 101, 9, 3);
}

.text-nav {
	display: flex;
	font-family: 'K2D', sans-serif;
	font-size: 1.2em;
	font-weight: bold;
	color: rgb(250, 248, 248);
}

.mir {
	font-size: 1.6em;
	text-shadow:
		-2px 2px 0 #000,
		1px 4px 0 #000;
}

.size {
	height: 4em;
}

@media screen and (max-width: 600px) {
	.text-nav {
		font-size: calc(1em - .3vmin);
	}
	.mir {
		font-size: calc(1.5em - .3vmin);
	}
	.garcia-nav {
		display: none;
	}
	nav ul a {
		padding: 0px 5px !important;
	}
}


.text-body {
	font-family: 'Montserrat', sans-serif;
	font-size: calc(1em + 1vmin); 
	text-align: justify;
}

.aboutme {
	color: #FFFFFF;
	margin-top: 5em;
	width: 100%;
	height: 140vh;
}

.photo-mir {
	height: 50vh;
	margin-top: 1.5em;
	border-radius: 3em;
}

.shadow {
	box-shadow: 1px -.5px 3px .5px rgb(7, 7, 6, 0.671);
}

.devicon {
	font-size: 7em;
	padding: 0.7rem .75rem !important;
	color: #212121;
	
}


@media screen and (max-width: 588px) {
	.aboutme {
		height: 100%;
	}
	.text-body {
		font-size: calc(.9em + 1vmin);
	}
	.photo-mir {
		display: flex;
		height: 35vh;
	}
}
.background-home {
    background: url(/static/media/backg.153be9ab.jpg) no-repeat center center;
    background-size: cover;
    height: 95vh;   
}
.background-div {
    width: 85%;
    height: 53vh;
    margin-top: 2em;
    border-radius: 2em
}


.home {
    width: 100%;
    height: 95vh;
}

.photo-home {
    width: 100%;
    height: 50vh;
    margin-top: 1em;
    border-radius: 2em;
}

.text-home {
    /* margin-top: 3em; */
	font-family: 'K2D', sans-serif;
	font-size: .9m;
	font-weight: bold;
    color: rgb(250, 248, 248);
	text-shadow:
		-1px 1px 0 #000,
		1px 4px 0 #000;
}

@media screen and (max-width: 588px) {
    .photo-home {
        margin-top: .5em;
        height: 25vh;
    }
    .text-home {
        margin-bottom: 5em;
    }
    .background-div {
        height: 30vh;
    }
}

.projects {
	font-size: calc(1em + 1vmin); 
	color: #FFFFFF;
	margin-top: 5em;
	width: 100%;
}

.card{
	height: 48vh;
	color: black;
}

.title-projects{
	margin-bottom: 1.5em;
}

@media screen and (max-width: 588px) {
	.title-projects{
		margin-top: 3em;
	}
	.projects {
		/* font-size: .5em;  */
		margin-top: 1em;
		height: 100%;
	}
	.card {
		height: 60vh;
	}
}

.contact {
	font-family: 'K2D', sans-serif;
  font-size: calc(.8em + 1vmin); 
	text-align: left;
	width: 100%;
}

.tex-link-f {
	color: #FFFFFF;
	width: 100%;
}

.icon-footer {
	height: 3rem;
	margin-top: .4em;
}

.devicon-footer {
	font-size: 2.4rem;
	margin-top: .4em;
}

